<template>
  <main id="app">
    <section ref="chatArea" class="chat-area">
      <p v-for="message in messages" :key="messages.indexOf(message)" class="message" :class="{ 'message-out': message.author === myName, 'message-in': message.author !== myName }">
        {{ message.body }} - {{ message.author }}
      </p>
      <p class="typing-indicator" v-show="typingUsers.length > 0">
        <span v-for="user in typingUsers" :key="user">
          {{ user }}
          <span v-if="typingUsers.length > 1">and</span>
        </span>
        <span v-if="typingUsers.length === 1">is</span>
        <span v-else-if="typingUsers.length > 1">are</span>
        typing...
      </p>
    </section>
    <section>
      <form class="chat-inputs" @submit.prevent="sendMessage">
        <input v-model="myMessage" autocomplete="off" type="text" placeholder="Type your message">
        <button type="submit">Send</button>
      </form>
    </section>

    <section class="chat-who">
      <h3>You are chatting as</h3>
      <input type="text" required v-model="myName" placeholder="Your Name">
    </section>

  </main>
</template>

<script>
export default {
  name: 'Chat',
  data () {
    return {
      messages: [],
      myName: '',
      myMessage: '',
      typingUsers: []
    }
  },
  methods: {
    sendMessage () {
      if (!this.myName) {
        alert('You must set your name before hitting send')
        return
      }
      let message = {
        author: this.myName,
        body: this.myMessage
      }
      console.log('message:', message)
      this.$socket.emit('message', message)
      this.messages.push(message)
      this.myMessage = ''
    }
  },
  watch: {
    myMessage () {
      if (this.myMessage) {
        console.log('I am typing')
        this.$socket.emit('typing', this.myName)
      } else {
        this.$socket.emit('notyping', this.myName)
      }
    }
  },
  sockets: {
    connect () {
      console.log('socket connected')
    },
    message (data) {
      console.log(data)
      if (data.author !== this.myName) {
        this.messages.push(data)
      }
    },
    typing (user) {
      console.log(user, ' is typing')
      if (!this.typingUsers.includes(user) && user !== this.myName) {
        this.typingUsers.push(user)
      }
    },
    notyping (user) {
      if (this.typingUsers.includes(user)) {
        let index = this.typingUsers.indexOf(user)
        this.typingUsers.splice(index, 1)
      }
    },
    disconnect () {
      console.log('socket disconnected')
    }
  }
}
</script>

<style scoped>
  .chat-area {
    /*   border: 1px solid #ccc; */
    background: white;
    height: 50vh;
    padding: 1em;
    overflow: auto;
    max-width: 350px;
    margin: 0 auto 0 auto;
    /*box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);*/
    position: relative;
  }
  .message {
    width: 45%;
    border-radius: 10px;
    padding: .5em;
    font-size: .8em;
  }
  .message-out {
    background: #407FFF;
    color: white;
    margin-left: 50%;
  }
  .message-in {
    background: #F1F0F0;
    color: black;
  }
  .chat-inputs {
    display: flex;
    justify-content: center;
    background: #ccc;
    max-width: 382px;
    margin: 0 auto;
    padding: .125em 0 .125em 0;
  }
  .chat-inputs input {
    padding: .5em;
    width: 80%;
    margin: 0;
  }
  .chat-inputs button {
    padding: .65em;
    margin: 0 .1em;
  }
  .chat-who {
    display: flex;
    justify-content: center;
  }
  .chat-who input {
    margin-left: .3em;
    height: 100%;
    align-self: center;
    padding: .2em;
  }
  .typing-indicator {
    position: absolute;
    bottom: 0;
    font-style: italic;
  }

</style>



